.stopwatch {
  box-sizing: border-box;
  border: 1vmin dotted #000;
  border-radius: 100%;
  width: 80vmin;
  height: 80vmin;
  position: fixed;
  bottom: 50%;
  right: 50%;
  transform: translate(50%, 50%);
}

.stopwatch-center {
  background-color: #f6a603;
  border-radius: 100%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  bottom: 50%;
  right: 50%;
  transform: translate(50%, 50%);
}

.stopwatch-seconds-hand {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate;
  position: absolute;
  bottom: 50%;
  right: 50%;
}

.stopwatch-seconds-hand--speed-up {
  animation-duration: 10s;
}

.stopwatch-minutes-hand {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate;
  position: absolute;
  bottom: 50%;
  right: 50%;
}

.stopwatch-minutes-hand--speed-up {
  animation-duration: 600s;
}

@keyframes rotate {
  from {
    transform: translate(50%)rotate(0);
  }

  to {
    transform: translate(50%)rotate(360deg);
  }
}

body {
  margin: 0;
}
